
.user{
    background-color: rgba(13, 52, 99, 0.31);
    margin-bottom:30px;
    width:560px;
    padding:20px;
    border-radius: 13px;
    }
    .user h3{
        color:rgb(226, 218, 107);
    }
    .user span
    {
    font-weight:500;
    color:black;
    }
    .user__bottom
    {
    margin-top:20px;
    }
    .user__delete
    {
    font-size: 14px;
    color:black;
    font-weight: bold;
    border:none;
    padding:10px;
    width:100px;
    background-color:rbga(255,0,0,0.818);
    border-radius:8px;
    cursor:pointer;
    }