*
{
    margin:0;
    padding:0;
}
body{
    background-color:rgb(89, 161, 201) ;
}
.home
{
    
    max-width:1000px;
    margin:0 auto;
    margin-top:160px;
}
.home__title
{
    color: rgb(238, 238, 30);
    font-size: 30px;
}
.home__container
{
    height:60vh;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}
.home__formContainer {
    box-shadow:1px 4px 12px -2px rgba(0,0,0,4.3);

    background:#356493;
    padding:30px;
    border-radius:10px;
    width:500px;
}
.home__formContainer input{
    border:1px solid #e5e5e5;
    outline:none;
    padding:15px 10px;
    width:80%;
    border-radius:8px;
    margin:10px 0;
    background:transparent;
    color:white;
    font-size:14px;
}
.home__formContainer input::placeholder
{
    color:white;
}
.home__formContainer button{
background: white;
border:1px solid white;
outline:none;
font-size:20px;
font-weight: 700;
color:rgb(26, 22, 22);
padding:10px 30px;
margin-top:30px;
border-radius:12px;
cursor:pointer;
}