*{
    margin: 0;
    padding: 0;
}
nav{
    background-color: rgb(12, 12, 82);
    color: rgb(209, 215, 221);
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}
nav ul{
    display: flex;
    justify-content: space-between;
    width: 45%;
    align-items: center;
}
nav ul a{
    font-size: 20px;
    font-weight: 300;
    color: aliceblue;
    text-decoration: none;
}